import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

import { User } from './user';


@Injectable({providedIn: 'root'})
export class UserService {

  private alumnosUrl = '/api/alumnos';

  httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };

  constructor(
    private http: HttpClient) { }



  getUser(): Observable<User[]> {
    console.log(this.alumnosUrl)
    return this.http.get<User[]>(this.alumnosUrl)
      .pipe(
        tap(_ => console.log('fetched alumnos')),
        catchError(this.handleError<User[]>('getUsers', []))
      );
  }

  addUser(user: User): Observable<User> {
    return this.http.post<User>(this.alumnosUrl, user, this.httpOptions).pipe(
        tap((newUser: User) => console.log(`added user w/ id=${newUser.id}`)),
        catchError(this.handleError<User>('addUser'))
    );
  }

  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error);
      return of(result as T);
    };
  }

}
